.sidebarr {
  width: 345px;
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  z-index: 100;
  background: #11493e;
  background: var(--main-color);
  transition: width 300ms;
  overflow-x: scroll;
}
.sidebar-brand {
  height: 90px;
  padding: 1rem 0rem 1rem 2rem;
  color: #fff;
}
.sidebar-brand span {
  display: inline-block;
  padding-right: 1rem;
  font-size: 30px;
}
.sidebar-menu li {
  width: 100%;
  margin-bottom: 0.6rem;
  padding-left: 0rem;
  list-style-type: none;
  /* background-color: red; */
}
.sidebar-menu {
  margin-top: 1rem;
}
.sidebar-menu a {
  color: #fff;
  font-size: 1.3rem;
  text-decoration: none;
  display: flex;
  padding: 12px 0 12px 20px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  transition: all ease-in-out 0.3s;
}
.sidebar-menu a:hover {
  background-color: white;
  color: #11493e;
}
#nav-toggle:checked + .sidebarr {
  width: 90px;
}
#nav-toggle:checked + .sidebarr .sidebar-brand,
#nav-toggle:checked + .sidebarr li span {
  /* padding-left: 1rem; */
  text-align: center;
}
#nav-toggle:checked + .sidebarr li a {
  /* background-color: red;
  margin-right: 30px; */

  align-items: center;
  display: flex;
  justify-content: center;
}
#nav-toggle:checked + .sidebarr .sidebar-brand h1 span:last-child,
#nav-toggle:checked + .sidebarr li a span:last-child {
  display: none;
}
.sidebar-menu a span:first-child {
  font-size: 1.8rem;
  margin-right: 30px;

  /* margin-bottom: 20px; */
  align-items: center;
}

#nav-toggle:checked ~ .main-content {
  margin-left: 90px;
}
#nav-toggle:checked ~ .main-content header {
  width: calc(100% - 70px);
  left: 90px;
}
